.container{
    height: 100vh;
}

.container .categoryList {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.container .categoryList .cards .card{
    background-color: bisque;
    text-align: center;
}

.container .categoryList .cards .card .content .button{
    padding: 1em 2em;
    margin: 5px;
    color: green;
    background-color: transparent;
    border: 2px solid green;
    border-radius: 10px;
    font-weight: bold;
    cursor: pointer;
}


.container .categoryList .cards .card .content .button:hover{
    background-color: green;
    color: #fff;
}

.cart-icon{
    position: fixed;
    top: 0%;
    right: 0%;
}