.products{
    height: 100vh;
}
.products .functions{
    height: 100vh;
    background-color: #eaeaea;
}


.productList a{
    display: block;
}

.products .functions .button{
    padding: 1em 2em;
    color: rgb(114, 192, 114);
    font-weight: bold;
    border: 2px solid rgb(114, 192, 114);
    border-radius: 5px;
    box-shadow: 3px 3px 3px 3px rgba(0,0,0,0.1);
    background: transparent;
    margin: 5px 0;
}

.products .functions .button:hover{
    background-color: rgb(114, 192, 114);
    box-shadow: 3px 3px 3px 3px rgba(0,0,0,0.3);
    color: #fff;
}

.products .functions .searchBar input{
    border: none;
    outline: none;
    color: white;
    font-weight: bold;
    padding: 5px 0;
    background-color: rgb(179, 214, 179);
    border-radius: 5px;
    display:block;
    width: auto;
}

.products .productList{
    display: flex;
    margin: 50px auto;
    align-items: start;
    flex-direction: row;
    justify-content: space-between;
}

.products .productList .product{
    display: block;
    width: 100%;
    color: black;
    margin: 10px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.products .productList .product .header, .price{
    color: black;
    font-weight: bold;
}

@media only screen and (max-width: 500px){
    
    .products{
        font-size: 10px;
    }
}

.products .productList .product img{
    box-shadow: 0px 0px 5px 5px rgb(179, 214, 179);
}

.cart-icon{
    display: fixed;
    z-index: 1000;
    top: 0%;
    right: 0%;
}